<template>
  <div class="app-page page-content-bottom-menu home">
    <page-content class="page-content">
      <cb-scroll
        ref="contentScroll"
        :data="recommendedList"
        :options="scrollData.options"
        :scroll-events="['scroll', 'scroll-end']"
        @scroll="topSearchData.show = false"
        @scroll-end="topSearchData.show = true"
        @pulling-down="onPullingDown"
        @pulling-up="onPullingUp">
        <div class="top-search vertical-center level-center" v-show="topSearchData.show">
          <van-field
            @focus="goSearch"
            v-model="topSearchData.value"
            clearable
            icon="search"
            placeholder="在这里输入搜索商品"
            @click-icon="$toast('question')"/>
          <div @click="goMessagecenter()" class="xiao-xi">
            <img src="/static/images/commonicon/xx.png"/>
          </div>
        </div>
        <div class="top-img">
          <img src="/static/images/home/banner.png"/>
        </div>
        <div class="value-meal">
          <title-second title="超值套餐" :hideRight="false" toPath=""></title-second>
          <div class="product-content">
            <div class="product-item" @click="goRecommendedDetail(1)" v-for="(item, i) in 4" :key="i">
              <div class="item-img">
                <img src="/static/images/home/tc1.png"/>
              </div>
              <div class="item-name">
                <div>超值套餐</div>
              </div>
            </div>
          </div>
        </div>
        <div class="recommended-product">
          <title-second title="推荐商品" :hideRight="true"></title-second>
          <div class="product-content">
            <div class="product-items">
                <div class="product-item" @click="goRecommendedDetail(1)" v-for="(item, i) in recommendedList" :key="i">
                  <lazy-component>
                    <div class="item-img">
                      <img v-lazy="'/static/images/home/yz1.png'"/>
                    </div>
                    <div class="item-name"><p class="font-ellipsis">澳大利亚进口红柚柚子柚子柚子红柚柚子柚子柚子</p></div>
                    <div class="item-sell-info">
                      <div>￥&nbsp;23.8</div>
                      <div>2.5kg</div>
                    </div>
                  </lazy-component>
                </div>
            </div>
          </div>
        </div>
      </cb-scroll>
    </page-content>
  </div>
</template>
<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
import CbScroll from '@/components/cube/scroll'
export default {
  name: 'home',
  components: { PageContent, TitleSecond, CbScroll },
  data () {
    return {
      // 头部搜索框数据
      topSearchData: {
        show: true,
        value: ''
      },
      // 推荐商品list
      recommendedList: [1, 1, 1, 1, 1, 1, 1, 1],
      scrollData: {
        options: {
          scrollbar: {
            fade: true
          },
          pullDownRefresh: {
            threshold: '90',
            txt: '刷新成功'
          },
          pullUpLoad: {
            threshold: '',
            txt: {
              more: '加载更多',
              noMore: '没有更多数据'
            },
            scrollbar: true
          }
        }
      }
    }
  },
  methods: {
    goSearch () {
      this.$router.push('/search')
    },
    goRecommendedDetail (id) {
      this.$router.push({path: '/category/productdetail', query: { id: id }})
    },
    goMessagecenter () {
      this.$router.push('/messagecenter')
    },
    beforeScrolStart () {
      console.log('aaa')
      this.topSearchData.show = false
    },
    onPullingDown () {
      setTimeout(() => {
        this.recommendedList = [1, 1, 1, 1, 1, 1, 1, 1]
      }, 1000)
    },
    onPullingUp () {
      setTimeout(() => {
        if (this.recommendedList.length < 14) {
          for (let i = 0; i < 3; i++) {
            this.recommendedList.push(1)
          }
        } else {
          this.$refs.contentScroll.forceUpdate()
        }
      }, 1000)
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .home{
    .top-search{
      position: absolute;
      top: 0;
      width: 100%;
      height: 100px;
      background-color: rgba(0, 0, 0, 0.4);;
      /deep/ .van-field{
        padding: 0 20px;
        background-color: white;
        line-height: 60px;
        width: 80%;
        border-radius: 30px;
        .van-cell__value{
          background-color: white;
          input::-webkit-input-placeholder{
            font-family: $font-family-default;
            color: $blue8BBECD;
          }
          i{
            color: $blue8BBECD;
          }
        }
      }
      .xiao-xi{
        width: 50px;
        margin-left: 10px;
        >img{
          width: 100%;
        }
      }
    }
    .top-img{
      width: 100%;
      height: 360px;
      img{
        width: 100%;height: 100%;
      }
    }
    .value-meal{
      margin-top: 20px;
      padding: 0 30px;
      .product-content{
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        .product-item{
          width: 23%;
          height: 180px;
          .item-img{
            width: 100%;
            height: 150px;
            img{
              width: 100%;
              height: 100%;
            }
          }
          .item-name{
            height: calc(100% - 150px);
            display: flex;
            justify-content: center;
            align-items: flex-end;
            color: $black333333;
            font-size: 24px;
            font-weight: 500;
          }
        }
      }
    }
    .recommended-product{
      margin-top: 50px;
      padding: 0 30px 10px 30px;
      .product-content{
        .product-items{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .product-item{
              width: 47%;
              height: 350px;
              margin-top: 20px;
              .item-img{
                width: 100%;
                height: 274px;
                img{
                  width: 100%;
                  height: 100%;
                }
              }
              .item-name{
                width: 100%;
                height: 38px;
                display: flex;
                justify-content: center;
                p{
                  color: $black333333;
                  font-size: 28px;
                  font-weight: 600;
                }
              }
              .item-sell-info{
                height: 38px;
                display: flex;
                justify-content: space-between;align-items: flex-end;
                color: $black333333;
                font-size: 28px;
                div:first-child{
                  color: $redff3b2f;
                }
                div:last-child{
                  color: $black999999;
                }
              }
            }
          }
      }
    }
  }
</style>



